import React from "react"
import PropTypes from "prop-types"
import withLocation from "./withLocation"
import CryptoJS from "crypto-js"


const DecryptComponent = ({ search, data }) => {
  const { key } = search
  if (!key) {
    return <p>Welcome Stranger, how did you wander here?</p>
  }
  // Encrypt
  //var ciphertext = CryptoJS.AES.encrypt(data, key).toString();

  // Decrypt
  var bytes  = CryptoJS.AES.decrypt(data, key);
  var originalText = bytes.toString(CryptoJS.enc.Utf8);

  return <p style={{ whitespace: "nowrap"}} dangerouslySetInnerHTML={{ __html: originalText }} />
  }

DecryptComponent.propTypes = {
  search: PropTypes.object,
  data: PropTypes.string,
}

export default withLocation(DecryptComponent)
